/*header,log, nav*/

.page-header {
    @media (max-width:800px) {
    position: relative;   
    }
}

.page-header .container {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0 3px 6px #00000029;
}

.page-header-logo {
    font-size: 32px;
    color: var(--primary-color);
    font-weight: 300;    
    margin-left: 50px;
    padding: 19px 0;
}

.hamburger {
    display: none;
    padding: 10px;
    cursor: pointer;
    border: none;
    background-color: transparent;

    @media (max-width:800px) {
        display: inline-block;
        position: absolute;
        right: 0;
        margin-right: 40px;

        &:hover {
            .hamburger-content {
                background-color: transparent;
            }
            .hamburger-content::before {
                transform: translateY(10px) rotate(45deg);
            }
            .hamburger-content::after {
                transform: translateY(-10px) rotate(-45deg);
            }
        }
    }
}

.hamburger-box {
    width: 34px;
    height: 24px;
    display: inline-block;
    position: relative;
}

.hamburger-content {
    @include hamburger-line;
    right: 0;
    top: 50%;
    transform: translate(0,-50%);

}

.hamburger-content::before, .hamburger-content::after {
    @include hamburger-line;
    content: "";
    right: 0;
    transition: transform 0.5s;
}

.hamburger-content::before {
    top: -10px;
}


.hamburger-content::after { 
    top: 10px;
}

.page-header-nav {
    @media (max-width:800px) {
            //display: none;
    }
}

.page-header-nav-menu {
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.page-header-nav-menu li {
    height: 70px;
    border-bottom: 4px solid white;
    padding: 25px 50px;
    color: var(--color-grey);
}

.page-header-nav-menu li:hover {
    border-bottom: 4px solid var(--primary-color);
    color: black;
}

.page-header-nav-menu a {
    color: inherit;
    font-family: var(--secondary-font);
    font-size: 11px;
    font-weight: bold;
    text-transform: uppercase;
    text-decoration: none;
    height: 100%;
}

@media (max-width:800px) {

.page-header-nav-menu {
    position: fixed;
    left: -2330px;
    top: 70px;
    background: white;
    flex-direction: column;
    height: 180px;
    width: 100%;
    justify-content: flex-start;
    align-items: flex-start;
    padding: 20px;
    box-sizing: border-box;
    box-shadow: 0 3px 6px #00000029;

    transition: 0.5s;  
    }
.page-header-nav-menu.show {
    left: 0;
    }


.page-header-nav .page-header-nav-menu li {
        height: 30px;
        border-bottom: 4px solid white;
        padding: 15px 50px;
        color: var(--color-grey);
        letter-spacing: 1px;
    }
    
.page-header-nav .page-header-nav-menu li:hover {
        color: black;
    }
}